.photo {
    width: 100%;
    height: calc(100vh - 60px);
    margin-top: 50px;
    background-image: url("../Assets/Image/myPhoto.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: 1019px) {
        display: none;
    }
}

